import axios from './axios'

// 查询文件列表
export function fileSelect (data) {
  return axios.get('/wen/select', { params: data }).then((res) => res.data)
}
// 删除文件
export function fileDel (data) {
  return axios.delete('/wen/delete', { params: data }).then((res) => res.data)
}
// 添加文件
export function fileAdd (data) {
  return axios.post('/wen/create', data).then((res) => res.data)
}


