<template>
  <div class="home">
    <div class="nav">
      <div  class="navs clearfix">
        <div class="nav-left">
          <!-- <el-input type="text" class="search" placeholder="请输入关键字">
            <el-button slot="append" icon="el-icon-search"></el-button>
          </el-input> -->
          <img src="../assets/image/logo.png" alt="" @click="toHome">
        </div>
        <div class="nav-right">
          <ul class="clearfix">
            <li v-for="(item, i) in navList"
              v-if="item.admin && username || item.name != '后台管理'"
              :key="i"
              :class="activeIndex == i ? 'activeClass' : '' "
              @click="toRouter(item.path, i)"><span>{{ item.name }}</span></li>
            <li @click="toEamil">邮件系统</li>
            <li @click="loginDialog = true" v-if="!username">登录</li>
            <li v-if="username">
              <el-dropdown style="color: #fff;">
                <span class="el-dropdown-link">
                  <span>{{username}}</span><i class="el-icon-arrow-down el-icon--right"></i>
                </span>
                <el-dropdown-menu slot="dropdown" style="margin-top:0px;">
                  <el-dropdown-item @click.native="logout">退出系统</el-dropdown-item>
                </el-dropdown-menu>
              </el-dropdown>
            </li>
          </ul>
        </div>
      </div>
    </div>
    <div class="top-img">
      <img src="../assets/image/first.jpg" alt="" v-if="activeIndex == 0">
      <img src="../assets/image/one.jpg" alt="" v-else-if="activeIndex == 1" />
      <img src="../assets/image/culture.jpg" alt="" v-else-if="activeIndex == 2" />
      <img src="../assets/image/dang.jpg" alt="" v-else-if="activeIndex == 3" />
      <img src="../assets/image/notice.jpg" alt="" v-else-if="activeIndex == 4" />
      <img src="../assets/image/news.jpg" alt="" v-else-if="activeIndex == 5" />
      <img src="../assets/image/big2.jpg" alt="" v-else-if="activeIndex == 6" />
      <img src="../assets/image/file.jpg" alt="" v-else-if="activeIndex == 7" />
    </div>
    <router-view class="content-box"></router-view>
    <div class="footer">
      <div class="footers">
        <div class="footer-top">
          <div class="left">
            <div class="logo-box">
              <img src="../assets/image/touming.png" alt="logo">
            </div>
            <div class="small-box clearfix">
              <!-- <div class="oneImg">
                <img src="../assets/image/small1.jpg" alt="">
                <p>集团党委</p>
              </div>
              <div class="oneImg">
                <img src="../assets/image/small2.jpg" alt="">
                <p>集团内网</p>
              </div>
              <div class="oneImg">
                <img src="../assets/image/small3.jpg" alt="">
                <p>集团c6平台</p>
              </div>
              <div class="oneImg">
                <img src="../assets/image/small4.jpg" alt="">
                <p>集团外网</p>
              </div>
              <div class="oneImg">
                <img src="../assets/image/small5.jpg" alt="">
                <p>集团工会</p>
              </div>
              <div class="oneImg">
                <img src="../assets/image/small6.jpg" alt="">
                <p>党建云平台</p>
              </div> -->
              <p>公司名称：彩虹集团（邵阳）特种玻璃有限公司</p>
              <p>地&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;址：湖南省邵阳市双清区彩虹集团（邵阳）特种玻璃有限公司</p>
              <p><span>联系电话：8929888</span><span style="margin-left: 40px;">邮编：422000</span></p>
            </div>
          </div>
          <div class="right">
            <img src="../assets/image/fewm.gif" alt="">
          </div>
        </div>
        <div class="footer-bottom clearfix">
          <span class="bottom-left">
            <span class="gray">网站版权：</span>
            <span class="blue">彩虹集团智能制造及大数据事业部</span>
          </span>
          <span class="bottom-right">
            <span class="gray">网站开发维护：</span>
            <span class="blue">彩虹集团智能制造及大数据事业部</span>
          </span>
        </div>
      </div>
    </div>
    <!-- 登录弹框 -->
    <el-dialog
      title="用户登录"
      :visible.sync="loginDialog"
      :before-close="closeDialog"
      width="30%">
      <div class="login-dialog">
        <el-form :model="ruleForm" :rules="rules2" ref="ruleForm" label-width="130px" class="demo-ruleForm">
          <el-form-item label="用户名" prop="admin">
            <el-input type="text" v-model="ruleForm.admin" placeholder="请输入用户名"></el-input>
          </el-form-item>
          <el-form-item label="密码" prop="password">
            <el-input v-if="isPass" type="password" v-model="ruleForm.password" placeholder="请输入密码" @keyup.enter.native="submitForm('ruleForm')" ref="pass">
              <i
                class="el-icon-view el-input__icon"
                style="cursor: pointer;"
                @click="changePass"
                slot="suffix">
              </i>
            </el-input>
            <el-input v-if="!isPass" type="text" v-model="ruleForm.password" placeholder="请输入密码" @keyup.enter.native="submitForm('ruleForm')" ref="pass">
              <i
                @click="changePass"
                style="cursor: pointer;"
                class="iconfont icon-eyeclose"
                slot="suffix">
              </i>
            </el-input>
          </el-form-item>
        </el-form>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="resetForm('ruleForm')">取 消</el-button>
        <el-button type="primary" @click="submitForm('ruleForm')">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import {success, notice} from '@/api/index'
export default {
  name: 'home',
  data () {
    return {
      loginDialog: false,
      isNow: false,
      // activeIndex: 0,
      ruleForm: {
        admin: '',
        password: ''
      },
      rules2: {
        admin: [
          { required: true, message: '用户名不能为空', trigger: 'blur' }
        ],
        password: [
          { required: true, message: '密码不能为空', trigger: 'blur' }
        ]
      },
      navList: [
        { name: '主页', path: '/' },
        { name: '企业文化', path: '/culture' },
        { name: '规章制度', path: '/rules' },
        { name: '公司新闻', path: '/news' },
        { name: '通知通告', path: '/notice' },
        { name: '生产信息', path: '/productInfo' },
        { name: '党建专栏', path: '/partyBuild' },
        { name: '文件下载', path: '/download' },
        { name: '后台管理', path: '/manage', admin: 1 }
      ],
      user: {
        name: '',
        userId: []
      },
      isPass: true
    }
  },
  computed: {
    // activeIndex: {
    //   get () {
    //     return this.$store.state.indexActive
    //   },
    //   set () {}
    // },
    username () {
      return this.$store.state.username
    }
  },
  methods: {
    changePass () {
      this.isPass = !this.isPass
    },
    toHome () {
      this.$router.push('/index')
      this.$store.commit('CHANGEINDEX', 0)
    },
    // 退出
    logout () {
      const path = document.getElementById('contextPath').value
      window.location.href = `${path}`
      this.$store.commit('USERINFO', {name: '', userid: ''})
      this.$store.commit('CHANGEINDEX', 0)
    },
    toEamil () {
      window.open('http://192.168.22.6')
    },
    toRouter (r, val) {
      this.activeIndex = val
      // this.$store.commit('CHANGEINDEX', val)
      this.$router.push(r)
    },
    submitForm (formName) {
      this.$refs[formName].validate(async (valid) => {
        if (valid) {
          await this.$store.dispatch('userLogin', {
            username: this.ruleForm.admin,
            password: this.ruleForm.password
          }).then((res) => {
            if (res && res.code === '00' && res.data && res.data.list && res.data.list.length > 0) {
              this.user = {
                name: res.data.list[0].name,
                userid: res.data.list[0].userid,
                accountname: res.data.list[0].username
              }
              this.$store.commit('USERINFO', this.user)
              success('登录成功')
              this.loginDialog = false
            } else if (res && res.code === '01') {
              notice('密码错误，请重新输入')
              this.ruleForm.password = ''
            }
          })
        } else {
          return false
        }
      })
    },
    resetForm (formName) {
      this.$refs[formName].resetFields()
      this.loginDialog = false
    },
    closeDialog () {
      this.ruleForm.admin = ''
      this.ruleForm.password = ''
      this.loginDialog = false
    }
  },
  created () {
    this.activeIndex = this.$route.meta.page
  },
  watch: {
    $route (to, from) {
      this.activeIndex = to.meta.page
      if (to.meta.page === 8 && to.path === '/manage/manageAccount') {
        let uid = this.$store.state.userid
        if (uid && uid.split(',').length === 7) {
          this.$router.push('/manage/manageAccount')
        } else if (uid && uid.split(',').length >= 1) {
          this.$router.push('/manage/manageArticle')
        } else {
          this.$router.push('/manage/manageFile')
        }
      }
    }
  }
}
</script>

<style lang="scss" scoped>
.home {
  width: 100%;
  min-width: 1200px;
  height: auto;
  margin: 0 auto;
  font-size: 14px;
  overflow-y: auto;
  > .nav {
    height: 50px;
    background: #385C74;
    line-height: 50px;
    .navs {
      width: 1200px;
      margin: 0 auto;
      > .nav-left {
        float: left;
        box-sizing: border-box;
        width: 150px;
        line-height: 50px;
        text-align: left;
        // > .search {
        //   width: 200px;
        //   height: 30px;
        // }
        // .el-input__inner {
        //   height: 30px;
        //   background: #2F73A0;
        //   border: 1px solid #2F73A0;
        // }
        img {
          width: 114px;
          vertical-align: middle;
          cursor: pointer;
        }
      }
      > .nav-right {
        float: right;
        width: calc(100% - 150px);
        padding-right: 50px;
        box-sizing: border-box;
        > ul {
          float: right;
          li {
            float: left;
            width: 90px;
            color: #fff;
            cursor: pointer;
            > span {
              display: inline-block;
              width: 90px;
              height: 48px;
            }
          }
          .activeClass {
            > span {
              color: #409EFF;
              border-bottom: 2px solid #409EFF;
            }
          }
        }
      }
    }
  }
  .el-carousel {
    img {
      width: 100%;
    }
  }
  > .top-img {
    padding: 0;
    img {
      width: 100%;
      min-width: 1200px;
    }
  }
  > .content-box {
    width: 1200px;
    min-height: calc(100vh - 380px);
    box-sizing: border-box;
    background: #fff;
    margin: 20px auto;
  }
  > .footer {
    // height: 300px;
    // text-align: left;
    // box-sizing: border-box;
    // background: #656565;
    > .footers {
      width: 100%;
      min-width: 1200px;
      height: 280px;
      text-align: left;
      box-sizing: border-box;
      background: #2b3235;
      > .footer-top {
        width: 1200px;
        margin: 0 auto;
        height: 240px;
        border-bottom: 1px solid #394145;
        box-sizing: border-box;
        padding-top: 20px;
        .logo-box {
          > img {
            width: 300px;
          }
        }
        .small-box {
          margin-top: 50px;
          > p {
            color: #fff;
            line-height: 25px;
            font-size: 12px;
          }
          // div:first-child {
          //   margin-top: 50px;
          //   float: left;
          //   width: 70%;
          // }
          // div:last-child {
          //   float: right;
          //   width: 30%;
          //   img {
          //     width: 200px;
          //   }
          // }
          // .oneImg {
          //   float: left;
          //   width: 100px;
          //   height: 150px;
          //   margin-right: 60px;
          //   text-align: center;
          //   > img {
          //     width: 106px;
          //     height: 90px;
          //     border: 2px solid #fff;
          //   }
          //   > p {
          //     color: #fff;
          //     line-height: 30px;
          //     font-size: 12px;
          //   }
          // }
        }
        .left {
          float: left;
          width: 70%;
        }
        .right {
          float: right;
          width: 30%;
          text-align: center;
          img {
            width: 150px;
          }
        }
      }
      > .footer-bottom {
        width: 1200px;
        height: 40px;
        margin: 0 auto;
        line-height: 40px;
        box-sizing: border-box;
        text-align: center;
        // background: #656565;
        box-sizing: border-box;
        > .bottom-left,.bottom-right {
          .gray {
            color: #fff;
          }
          .blue {
            color: #2A97D0;
          }
        }
        .bottom-right {
          // margin-left: 500px;
          float: right;
        }
        .bottom-left {
          float: left;
        }
      }
    }
  }
  .login-dialog {
    text-align: left;
    .el-input {
      width: 90%;
    }
  }
  .el-input__suffix {
    width: 25px;
  }
}
</style>



