<template>
  <div class="manage-two">
    <!-- 文档管理 -->
    <div class="manage-add">
      <el-button type="primary" size="mini" @click="addNew">新增</el-button>
      <el-button type="success" size="mini" @click="getList">刷新</el-button>
    </div>
    <div class="choose">
      <span>文章类型：</span>
      <el-radio-group v-model="type" @change="radioChange">
        <el-radio :label="0" v-if="$store.state.userid.indexOf(1) != -1 && $store.state.userid.indexOf(2) != -1 && $store.state.userid.indexOf(3) != -1 && $store.state.userid.indexOf(4) != -1">全部</el-radio>
        <el-radio :label="1" v-if="$store.state.userid.indexOf(1) != -1">公司新闻</el-radio>
        <el-radio :label="2" v-if="$store.state.userid.indexOf(2) != -1">通知通告</el-radio>
        <el-radio :label="3" v-if="$store.state.userid.indexOf(3) != -1">生产信息</el-radio>
        <el-radio :label="4" v-if="$store.state.userid.indexOf(4) != -1">党建专栏</el-radio>
        <el-radio :label="5" v-if="$store.state.userid.indexOf(5) != -1">企业文化</el-radio>
        <el-radio :label="6" v-if="$store.state.userid.indexOf(6) != -1">规章制度</el-radio>
        <el-radio :label="7" v-if="$store.state.userid.indexOf(7) != -1">工团建设</el-radio>
      </el-radio-group>
    </div>
    <div class="tabs">
      <el-table
        :data="tableData"
        style="width: 100%">
        <el-table-column
          prop="titleofarticle"
          label="标题">
        </el-table-column>
        <el-table-column
          prop="authorofthzarticle"
          label="作者"
          width="100">
        </el-table-column>
        <el-table-column
          prop="time"
          label="日期"
          width="160">
        </el-table-column>
        <el-table-column label="操作" width="150">
          <template slot-scope="scope">
            <el-button
              size="mini"
              @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
            <el-button
              size="mini"
              type="danger"
              @click="handleDelete(scope.$index, scope.row)">删除</el-button>
          </template>
        </el-table-column>
      </el-table>
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="currentPage"
        :page-sizes="[10, 20, 30, 40, 50]"
        :page-size="pageNum"
        layout="total, sizes, prev, pager, next, jumper"
        :total="totalCount">
      </el-pagination>
    </div>
    <!-- 新增或编辑 -->
    <el-dialog
      :before-close="handleClose"
      :title="isChange ? '编辑文章' : '新增文章'"
      :visible.sync="addNewDialog"
      v-if="addNewDialog"
      :close-on-click-modal="false"
      top="2%"
      width="70%">
      <el-form :model="ruleForm" status-icon :rules="rules2" ref="ruleForm" label-width="110px" class="demo-ruleForm">
        <el-form-item label="文章标题" prop="titleofarticle">
          <el-input type="text" v-model="ruleForm.titleofarticle" placeholder="请输入文章标题"></el-input>
        </el-form-item>
        <el-form-item label="文章作者" prop="authorofthzarticle">
          <el-input type="text" v-model="ruleForm.authorofthzarticle" placeholder="请输入作者名称"></el-input>
        </el-form-item>
        <el-form-item label="图片新闻">
          <el-switch
            v-model="ruleForm.isPicture"
            active-text="ON"
            inactive-text="OFF">
          </el-switch>
        </el-form-item>
        <el-form-item label="图片选择" prop="picture" v-if="ruleForm.isPicture">
          <el-upload
            :action="action"
            list-type="picture-card"
            :before-upload="beforeImgUpload"
            :on-success="handleImgSuccess"
            :on-error="handleImgError"
            :on-exceed="handleExceed"
            :on-remove="handleImgMove"
            :limit="1"
            :file-list="fileList"
            :data="datas"
            :class="hasImg ? 'imgicon' : ''"
            name="file">
            <i class="el-icon-plus"></i>
          </el-upload>
        </el-form-item>
        <el-form-item label="所属栏目" prop="columntype">
          <el-select v-model="ruleForm.columntype" placeholder="请选择">
            <el-option
              v-for="item in options"
              :disabled="item.disabled"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="文章内容" prop="contentofthearticle">
          <ueditor
            @cont="getContent"
            :mode='ruleForm.contentofthearticle'
            ref="editor"></ueditor>
        </el-form-item>
        <el-form-item label="附件">
          <el-upload
            class="upload-demo"
            :class="hasFile ? 'fileicon' : ''"
            :action="action"
            :before-upload="filebeforeImgUpload"
            :on-remove="filehandleRemove"
            :on-success="filehandleImgSuccess"
            :on-error="filehandleImgError"
            :on-exceed="filehandleExceed"
            multiple
            :limit="1"
            name="file"
            :file-list="fufileList">
            <el-button size="small" type="primary">附件上传</el-button>
            <div slot="tip" class="el-upload__tip">文件上传支持 doc / docx / ppt / pptx / xls / xlsx / pdf / txt / png / jpg / jpeg / rar / zip 格式的文件</div>
          </el-upload>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="resetForm('ruleForm')">取 消</el-button>
        <el-button type="primary" @click="submitForm('ruleForm')">确 定</el-button>
      </span>
    </el-dialog>
    <!-- 删除弹框 -->
    <el-dialog
      title="删除"
      :visible.sync="deleteDialog"
      :close-on-click-modal="false"
      width="30%">
      <span>确定删除该文章吗？</span>
      <span slot="footer" class="dialog-footer">
        <el-button @click="deleteDialog = false">取 消</el-button>
        <el-button type="primary" @click="delSure">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import url from '@/baseUrl'
import {success, notice} from '@/api/index'
import ueditor from '@/components/ueditor'
// import {dateFormat} from '@/filters/index'
export default {
  name: 'manage-two',
  data () {
    var titleofarticle = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('文章标题不能为空'))
      } else {
        if (value.trim().length > 50) {
          callback(new Error('文章标题最大长度为50个字符'))
        }
        callback()
      }
    }
    var authorofthzarticle = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('作者名称不能为空'))
      } else {
        if (value.trim().length > 20) {
          callback(new Error('作者名称最大长度为20个字符'))
        }
        callback()
      }
    }
    return {
      datas: {
        token: '',
        key: 'e.jpg'
      },
      options: [
        {
          value: '1',
          label: '公司新闻',
          disabled: true
        },
        {
          value: '2',
          label: '通知通告',
          disabled: true
        },
        {
          value: '3',
          label: '生产信息',
          disabled: true
        },
        {
          value: '4',
          label: '党建专栏',
          disabled: true
        },
        {
          value: '5',
          label: '企业文化',
          disabled: true
        },
        {
          value: '6',
          label: '规章制度',
          disabled: true
        },
        {
          value: '7',
          label: '工团建设',
          disabled: true
        }
      ],
      value: '',
      tableData: [],
      ruleForm: {
        titleofarticle: '',
        authorofthzarticle: '',
        picture: '',
        isPicture: false,
        columntype: '',
        contentofthearticle: ''
      },
      rules2: {
        titleofarticle: [
          { required: true, validator: titleofarticle, trigger: 'blur' }
        ],
        authorofthzarticle: [
          { required: true, validator: authorofthzarticle, trigger: 'blur' }
        ],
        contentofthearticle: [
          { required: true, trigger: 'blur', message: '文章内容不能为空' }
        ],
        columntype: [
          { required: true, trigger: 'change', message: '请选择文章类型' }
        ]
      },
      addNewDialog: false,
      isChange: false,
      deleteDialog: false,
      currentPage: 0,
      pageNum: 10,
      totalCount: 0,
      dialogImageUrl: '',
      infoId: '',
      type: 0,
      infoUserId: '',
      fileList: [],
      fufileList: [],
      isAdmin: false,
      hasImg: false,
      hasFile: false,
      fileUrl: '',
      filename: ''
    }
  },
  components: {
    ueditor
  },
  methods: {
    handleEdit (index, row) {
      console.log(row)
      this.fileList = []
      this.fufileList = []
      this.isChange = true
      this.infoId = row.id
      this.hasImg = false
      this.hasFile = false
      if (row.picture && row.picture.length > 0) {
        let imgList = row.picture.split(',')
        this.fileList = imgList.map(item => ({
          url: item
        }))
        this.hasImg = true
      }
      if (row.fufile && row.fufile.length > 0) {
        let filesList = row.fufile.split(',')
        this.fufileList = filesList.map(item => ({
          url: item,
          name: row.fufilename
        }))
        this.hasFile = true
      }
      this.imgUrl = row.imgUrl
      this.fileUrl = row.fufile
      this.ruleForm = {
        titleofarticle: row.titleofarticle,
        authorofthzarticle: row.authorofthzarticle,
        isPicture: (row.picture && row.picture.length > 0) ? Boolean(1) : Boolean(0),
        contentofthearticle: row.contentofthearticle,
        columntype: Number(row.columntype) === 1
          ? '公司新闻' : (Number(row.columntype) === 2)
            ? '通知通告' : (Number(row.columntype) === 3)
              ? '生产信息' : (Number(row.columntype) === 4)
                ? '党建专栏' : (Number(row.columntype) === 5)
                  ? '企业文化' : (Number(row.columntype) === 6)
                    ? '规章制度' : (Number(row.columntype) === 7)
                      ? '工团建设' : ''
      }
      this.addNewDialog = true
    },
    handleDelete (index, row) {
      this.deleteDialog = true
      this.infoId = row.id
      this.infoUserId = row.columntype
    },
    addNew () {
      this.isChange = false
      this.fileList = []
      this.ruleForm = {
        titleofarticle: '',
        authorofthzarticle: '',
        picture: '',
        isPicture: false,
        columntype: '',
        contentofthearticle: ''
      }
      this.hasImg = false
      this.hasFile = false
      this.addNewDialog = true
    },
    // 新增
    submitForm (formName) {
      this.$refs[formName].validate(async (valid) => {
        if (valid) {
          if (this.isChange) {
            let types = this.ruleForm.columntype === '公司新闻'
              ? '1' : (this.ruleForm.columntype === '通知通告')
                ? '2' : (this.ruleForm.columntype === '生产信息')
                  ? '3' : (this.ruleForm.columntype === '党建专栏')
                    ? '4' : (this.ruleForm.columntype === '企业文化')
                      ? '5' : (this.ruleForm.columntype === '规章制度')
                        ? '6' : (this.ruleForm.columntype === '工团建设')
                          ? '7' : '' || this.ruleForm.columntype
            let datas = {
              id: this.infoId,
              authorofthzarticle: this.ruleForm.authorofthzarticle,
              columntype: types,
              contentofthearticle: this.ruleForm.contentofthearticle,
              picture: this.imgUrl,
              titleofarticle: this.ruleForm.titleofarticle,
              fufile: this.fileUrl,
              fufilename: this.filename
            }
            await this.$store.dispatch('articleUpdate', datas)
          } else {
            let datas = {
              authorofthzarticle: this.ruleForm.authorofthzarticle,
              columntype: this.ruleForm.columntype,
              contentofthearticle: this.ruleForm.contentofthearticle,
              picture: this.imgUrl,
              titleofarticle: this.ruleForm.titleofarticle,
              username: this.isAdmin ? '' : this.$store.state.accountname,
              fufile: this.fileUrl,
              fufilename: this.filename
            }
            await this.$store.dispatch('articleCreate', datas)
          }
          this.$refs[formName].resetFields()
          this.$refs.editor.clea()
          this.addNewDialog = false
          this.getList()
          this.imgUrl = ''
          this.hasImg = false
          this.hasFile = false
          this.fileList = []
        } else {
          return false
        }
      })
    },
    handleClose () {
      this.fileList = []
      this.imgUrl = ''
      // this.ruleForm = {
      //   titleofarticle: '',
      //   authorofthzarticle: '',
      //   picture: '',
      //   isPicture: false,
      //   columntype: '',
      //   contentofthearticle: ''
      // }
      this.addNewDialog = false
    },
    resetForm (formName) {
      this.$refs[formName].resetFields()
      this.ruleForm = {
        titleofarticle: '',
        authorofthzarticle: '',
        picture: '',
        isPicture: false,
        columntype: '',
        contentofthearticle: ''
      }
      this.addNewDialog = false
    },
    handleSizeChange (val) {
      this.pageNum = val
      this.getList()
    },
    handleCurrentChange (val) {
      this.currentPage = val
      this.getList()
    },
    handlePictureCardPreview (file) {
      this.dialogImageUrl = file.url
      this.dialogVisible = true
    },
    // 删除
    async delSure () {
      this.deleteDialog = false
      await this.$store.dispatch('articleDel', {
        groupId: this.infoId,
        userids: this.infoUserId
      }).then((res) => {
        this.getList()
      })
    },
    async getList () {
      this.tableData = []
      await this.$store.dispatch('articleGet', {
        pageSize: this.pageNum,
        pageNum: this.currentPage,
        type: Number(this.type) === 0 ? '' : this.type,
        username: this.isAdmin ? '' : this.$store.state.accountname
      }).then((res) => {
        if (res && res.list && res.list.length > 0) {
          this.tableData = res.list
          this.totalCount = res.total
        }
      })
    },
    // 单选框切换监听事件
    radioChange () {
      this.getList()
    },
    // 上传图片之前判断
    beforeImgUpload (file) {
      const isImage = file.type === 'image/jpeg' || file.type === 'image/png' || file.type === 'image/PNG' || file.type === 'image/JPG' || file.type === 'image/JPEG'
      const isLt30kb = file.size / 1024 / 1024 < 5

      if (!isImage) {
        this.$message.error('图片格式应该为 png / jpg / jpeg / JPG / JPEG / PNG')
      }
      if (!isLt30kb) {
        this.$message.error('图片大小不能超过 5M!')
      }
      return isImage && isLt30kb
    },
    // 上传logo图片成功
    handleImgSuccess (res, file, fileList) {
      this.imgUrl = res ? res.data : ''
      success('上传成功')
      this.hasImg = true
    },
    handleImgError (file, fileList) {
      fileList.splice(0, 1)
      this.hasImg = false
      notice('上传失败,请检查图片格式是否为png / jpg / jpeg / PNG / JPG / JPEG')
    },
    filehandleImgError (file, fileList) {
      fileList.splice(0, 1)
      this.hasFile = false
      notice('上传失败,请检查图片格式是否为 doc / docx / ppt / pptx / xls / xlsx / pdf / txt / png / jpg / jpeg / rar / zip ')
    },
    // 移除图片图片
    handleImgMove (file, fileList) {
      // this.imgList = fileList
      this.imgUrl = ''
      this.hasImg = false
    },
    handleExceed (files, fileList) {
      notice('最多上传一张图片')
    },
    getContent (info) {
      this.ruleForm.contentofthearticle = info
    },
    filehandleRemove (file, fileList) {
      fileList = []
      this.fileUrl = ''
      this.filename = ''
      this.hasFile = false
    },
    filehandleExceed (files, fileList) {
      notice('最多上传一个文件')
    },
    filehandleImgSuccess (res, file, fileList) {
      this.fileUrl = res ? res.data : ''
      this.filename = res ? res.path : ''
      success('上传成功')
      this.hasFile = true
    },
    filebeforeImgUpload (file) {
      return new Promise(async (resolve, reject) => {
        const type = file.name.split('.')[file.name.split('.').length - 1].toLowerCase()
        if ((type === 'pdf' || type === 'doc' || type === 'docx' || type === 'ppt' || type === 'pptx' || type === 'xls' || type === 'xlsx') && file.size / 1024 / 1024 > 200) {
          this.$message.error('文档最大为200M')
          reject(new Error('文档最大为200M'))
          return
        }
        if ((type === 'jpg' || type === 'png' || type === 'gif' || type === 'jpeg') && file.size / 1024 / 1024 > 5) {
          this.$message.error('图片文件最大为5M')
          reject(new Error('图片文件最大为5M'))
          return
        }
        if (type === 'txt' && file.size / 1024 / 1024 > 2) {
          this.$message.error('纯文本文件最大为2M')
          reject(new Error('纯文本文件最大为2M'))
          return
        }
        // if (type === 'epub' && file.size / 1024 / 1024 > 20) {
        //   this.$message.error('电子书文件最大20M')
        //   reject(new Error('电子书文件最大20M'))
        //   return
        // }
        // if ((type === 'mp3' || type === 'acc' || type === 'mp4' || type === 'mpg' || type === 'avi' || type === 'wmv') && file.size / 1024 / 1024 > 200) {
        //   this.$message.error('音视频文件最大为200M')
        //   reject(new Error('音视频文件最大为200M'))
        //   return
        // }
        if (file.size / 1024 / 1024 > 200) {
          this.$message.error('文件最大为200M')
          reject(new Error('文件最大为200M'))
          return
        }
        if (type !== 'txt' && type !== 'jpg' && type !== 'png' && type !== 'gif' && type !== 'jpeg' &&
            type !== 'pdf' && type !== 'doc' && type !== 'docx' && type !== 'ppt' && type !== 'pptx' && type !== 'xls' &&
            type !== 'xlsx' && type !== 'rar' && type !== 'zip') {
          this.$message.error('文件格式错误')
          reject(new Error('文件格式错误'))
          return
        }
        resolve()
      })
    }
  },
  computed: {
    action () {
      return url + '/upload'
    }
  },
  async created () {
    // let qns = await this.$store.dispatch('qn')
    // this.datas.token = qns.result.token
    let xi = this.$store.state.userid.split(',')
    this.options.forEach((item, i) => {
      xi.forEach((items, ind) => {
        if (item.value === xi[ind]) {
          item.disabled = false
        }
      })
    })
    if (xi && xi.length === 7) {
      this.type = 0
      this.isAdmin = true
    } else {
      this.type = Number(xi[0])
      this.isAdmin = false
    }
    this.getList()
  }
}
</script>

<style lang="scss">
.manage-two {
  text-align: left;
  .manage-add {
    height: 40px;
    line-height: 40px;
    border-bottom: 1px solid #efefef;
    padding-left: 20px;
    box-sizing: border-box;
  }
  > .choose {
    padding-top: 20px;
    padding-left: 20px;
    box-sizing: border-box;
  }
  .tabs {
    padding: 20px;
    .el-table th:first-child {
      border-top-left-radius: 0;
      border-bottom-left-radius: 0;
    }
    th {
      background: #f5f7fa;
    }
  }
  .el-form {
    .el-input {
      width: 90%;
    }
  }
  .el-pagination {
    margin-top: 20px;
    text-align: right;
  }
  .imgicon {
    .el-upload--picture-card {
      display: none;
    }
  }
  .fileicon {
    .el-upload {
      display: none;
    }
    .el-upload__tip {
      margin-top: 0;
    }
  }
}
</style>



