<template>
  <div class="manage clearfix">
    <div class="manage-nav">
      <el-menu
        :default-active="activePath"
        class="el-menu-vertical-demo"
        text-color="#333"
        router
        active-text-color="#409EFF">
        <el-menu-item index="/manage/manageAccount" v-if="isAdmin">
          <i class="iconfont icon-yuangongzhanghaoguanli"></i>
          <span slot="title">账号管理</span>
        </el-menu-item>
        <el-menu-item index="/manage/manageArticle" v-if="isAdmin || !isNone">
          <i class="iconfont icon-file2"></i>
          <span slot="title">文档管理</span>
        </el-menu-item>
        <el-menu-item index="/manage/manageFile">
          <i class="iconfont icon-file"></i>
          <span slot="title">文件管理</span>
        </el-menu-item>
      </el-menu>
    </div>
    <div class="manage-box">
      <router-view></router-view>
    </div>
  </div>
</template>

<script>
export default {
  name: 'manage',
  data () {
    return {
      activePath: '',
      isAdmin: false,
      isNone: false
    }
  },
  methods: {},
  created () {
    if (Number(this.$router.currentRoute.meta.small) === 1) {
      this.activePath = '/manage/manageAccount'
    } else if (Number(this.$router.currentRoute.meta.small) === 2) {
      this.activePath = '/manage/manageArticle'
    } else if (Number(this.$router.currentRoute.meta.small) === 3) {
      this.activePath = '/manage/manageFile'
    }
    if (this.$store.state.userid && this.$store.state.userid.split(',').length === 7) {
      this.isAdmin = true
      this.isNone = false
    } else {
      this.isAdmin = false
    }
    if (this.$store.state.userid === '') {
      this.isNone = true
      this.isAdmin = false
    }
  }
}
</script>

<style lang="scss">
.manage {
  > .manage-nav {
    float: left;
    width: 250px;
    border-right: 1px solid #efefef;
    box-sizing: border-box;
    > .el-menu {
      min-height: calc(100vh - 380px);
      margin-top: 20px;
      border-right: 0px;
      .iconfont {
        margin-right: 6px;
        width: 24px;
        text-align: center;
        font-size: 18px;
        vertical-align: middle;
      }
    }
  }
  > .manage-box {
    float: right;
    width: calc(100% - 250px);
    min-height: calc(100vh - 380px);
  }
}
</style>



