<template>
  <div class="download">
    <!-- 新闻 -->
    <div class="new-content">
      <div class="top-title">
        <span>文件下载</span>
        <div style="display: inline-block;float: right;margin-right: 20px;">
          <el-input
            placeholder="请输入内容"
            v-model="keyword">
            <i slot="suffix" class="el-input__icon el-icon-search" @click="getList"></i>
          </el-input>
        </div>
      </div>
      <div v-for="(item, i) in newList" :key="i" class="one-file">
        <div class="clearfix">
          <span v-if="item.fileheadings && item.fileheadings.length < 66">{{item.fileheadings}}</span>
          <span v-else>{{item.fileheadings.substring(0, 65) + '...'}}</span>
          <span>
            <a :href="item.fileenclosure" download class="download-a" Content-Disposition="attachment">点击下载</a>
          </span>
        </div>
        <div class="clearfix">
          <span style="display: inline-block;width: 200px;">上传人：{{item.filepeople}}</span>
          <span style="float: right;">上传时间：{{item.time}}</span>
        </div>
      </div>
      <el-pagination
        v-if="newList && newList.length > 0"
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="currentPage"
        :page-size="pageNum"
        background
        layout="total, sizes, prev, pager, next, jumper"
        :total="totalCount">
      </el-pagination>
    </div>
  </div>
</template>

<script>
// import {success, notice} from '@/api/index'
export default {
  name: 'download',
  data () {
    return {
      newList: [],
      currentPage: 0,
      totalCount: 0,
      keyword: '',
      pageNum: 10
    }
  },
  watch: {
    keyword () {
      this.getList()
    }
  },
  methods: {
    handleSizeChange (val) {
      this.pageNum = val
      this.getList()
    },
    handleCurrentChange (val) {
      this.currentPage = val
      this.getList()
    },
    download (item) {
      window.location.href = item.fileenclosure
    },
    async getList () {
      this.newList = []
      await this.$store.dispatch('fileSelect', {
        pageNum: this.currentPage,
        titleofarticle: this.keyword,
        pageSize: this.pageNum
      }).then((res) => {
        if (res && res.list && res.list.length > 0) {
          this.newList = res.list
          this.totalCount = res.total
        }
      })
    }
  },
  created () {
    this.getList()
  }
}
</script>

<style lang="scss">
.download {
  padding: 20px 0;
  > .new-content {
    .top-title {
      background: #356BA5;
    }
    .one-file {
      width: 100%;
      box-sizing: border-box;
      height: 100px;
      border-bottom: 1px solid #efefef;
      > div:first-child {
        line-height: 60px;
        > span:first-child {
          float: left;
        }
        > span:last-child {
          float: right;
          .download-a {
            padding: 8px 10px;
            background: #409EFF;
            border-radius: 2px;
            color: #fff;
          }
          .download-a:hover {
            background: #356BA5;
          }
        }
      }
      > div:last-child {
        box-sizing: border-box;
        span {
          color: #999;
          font-size: 12px;
        }
        > span:last-child {
          margin-left: 80px;
        }
      }
    }
  }
}
</style>



