<template>
  <div class="manage-three">
    <!-- 文件管理 -->
    <div class="manage-add">
      <el-button type="primary" size="mini" @click="addNew">新增</el-button>
      <el-button type="success" size="mini" @click="getList">刷新</el-button>
    </div>
    <div class="tabs">
      <el-table
        :data="tableData"
        style="width: 100%">
        <el-table-column
          prop="fileheadings"
          label="文件名">
        </el-table-column>
        <el-table-column
          prop="filetype"
          label="文件类型">
        </el-table-column>
        <el-table-column
          prop="filepeople"
          label="上传人"
          width="100">
        </el-table-column>
        <el-table-column
          prop="time"
          label="日期"
          width="160">
        </el-table-column>
        <el-table-column label="操作" width="80">
          <template slot-scope="scope">
            <el-button
              size="mini"
              type="danger"
              @click="handleDelete(scope.$index, scope.row)">删除</el-button>
          </template>
        </el-table-column>
      </el-table>
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="currentPage"
        :page-sizes="[10, 20, 30, 40, 50]"
        :page-size="pageNum"
        layout="total, sizes, prev, pager, next, jumper"
        :total="totalCount">
      </el-pagination>
    </div>
    <!-- 新增或编辑 -->
    <el-dialog
      title="新增文件"
      :visible.sync="addNewDialog"
      :close-on-click-modal="false"
      top="2%"
      width="40%">
      <el-form :model="ruleForm" status-icon :rules="rules2" ref="ruleForm" label-width="110px" class="demo-ruleForm">
        <el-form-item label="文件名称" prop="fileheadings">
          <el-input type="text" v-model="ruleForm.fileheadings" placeholder="请输入文章标题"></el-input>
        </el-form-item>
        <!-- <img src="../../../assets/image/fileType.png" alt="" style="margin-left: 30px;margin-bottom: 20px;" /> -->
        <el-form-item label="文件列表">
          <el-upload
            class="upload-demo"
            :action="action"
            :class="hasFile ? 'fileicon' : ''"
            :before-upload="beforeImgUpload"
            :on-remove="handleRemove"
            :on-success="handleImgSuccess"
            :on-exceed="handleExceed"
            multiple
            :limit="1"
            name="file"
            :file-list="ruleForm.fileList">
            <el-button size="small" type="primary">附件上传</el-button><div slot="tip" class="el-upload__tip">文件上传支持 doc / docx / ppt / pptx / xls / xlsx / pdf / txt / png / jpg / jpeg / rar / zip 格式的文件</div>
          </el-upload>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="resetForm('ruleForm')">取 消</el-button>
        <el-button type="primary" @click="submitForm('ruleForm')">确 定</el-button>
      </span>
    </el-dialog>
    <!-- 删除弹框 -->
    <el-dialog
      title="删除"
      :visible.sync="deleteDialog"
      :close-on-click-modal="false"
      width="30%">
      <span>确定删除该文章吗？</span>
      <span slot="footer" class="dialog-footer">
        <el-button @click="deleteDialog = false">取 消</el-button>
        <el-button type="primary" @click="delSure">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import {success, notice} from '@/api/index'
import url from '@/baseUrl'
// import {dateFormat} from '@/filters/index'
export default {
  name: 'manage-three',
  data () {
    return {
      tableData: [],
      ruleForm: {
        fileheadings: '',
        fileenclosure: '',
        fileList: []
      },
      rules2: {
        fileheadings: [
          { required: true, trigger: 'blur', message: '文件名称不能为空' }
        ]
        // authorofthzarticle: [
        //   { required: true, trigger: 'blur', message: '上传人名称不能为空' }
        // ],
        // fileList: [
        //   { type: 'array', required: true, message: '请至少选择1个文件', trigger: 'change' }
        // ]
      },
      addNewDialog: false,
      deleteDialog: false,
      currentPage: 0,
      pageNum: 10,
      totalCount: 0,
      dialogImageUrl: '',
      fileUrl: '',
      filename: '',
      isAdmin: false,
      hasFile: false,
      inifoId: ''
    }
  },
  computed: {
    action () {
      return url + '/upload'
    }
  },
  methods: {
    handleDelete (index, row) {
      this.inifoId = row.id
      this.deleteDialog = true
    },
    addNew () {
      this.ruleForm.fileheadings = ''
      this.ruleForm.fileList = []
      this.hasFile = false
      this.addNewDialog = true
    },
    submitForm (formName) {
      this.$refs[formName].validate(async (valid) => {
        if (valid) {
          await this.$store.dispatch('fileAdd', {
            fileheadings: this.ruleForm.fileheadings,
            filepeople: this.$store.state.username,
            fileenclosure: this.fileUrl,
            filename: this.filename,
            username: this.$store.state.accountname
          })
        } else {
          return false
        }
        this.addNewDialog = false
        this.ruleForm.fileList = []
        this.hasFile = false
        this.ruleForm.fileheadings = ''
        this.getList()
      })
    },
    resetForm (formName) {
      this.$refs[formName].resetFields()
      this.addNewDialog = false
    },
    handleSizeChange (val) {
      this.pageNum = val
      this.getList()
    },
    handleCurrentChange (val) {
      this.currentPage = val
      this.getList()
    },
    handleRemove (file, fileList) {
      fileList = []
      this.fileUrl = ''
      this.filename = ''
      this.hasFile = false
    },
    handleExceed (files, fileList) {
      notice('最多上传一个文件')
    },
    handleImgSuccess (res, file, fileList) {
      this.fileUrl = res ? res.data : ''
      this.filename = res ? res.path : ''
      success('上传成功')
      this.hasFile = true
    },
    // beforeImgUpload (file) {
    //   return new Promise(async (resolve, reject) => {
    //     const type = file.name.split('.')[file.name.split('.').length - 1].toLowerCase()
    //     if ((type === 'doc' || type === 'docx' || type === 'ppt' || type === 'pptx' || type === 'xls' || type === 'xlsx') && file.size / 1024 / 1024 > 20) {
    //       this.$message.error('OFFICE文档最大为20M')
    //       reject(new Error('OFFICE文档最大为20M'))
    //       return
    //     }
    //     if (type === 'pdf' && file.size / 1024 / 1024 > 10) {
    //       this.$message.error('pdf 文件最大为10M')
    //       reject(new Error('pdf 文件最大为10M'))
    //       return
    //     }
    //     if ((type === 'jpg' || type === 'png' || type === 'gif' || type === 'jpeg') && file.size / 1024 / 1024 > 10) {
    //       this.$message.error('图片文件最大只能10M')
    //       reject(new Error('图片文件最大只能10M'))
    //       return
    //     }
    //     if (type === 'txt' && file.size / 1024 / 1024 > 1) {
    //       this.$message.error('纯文本文件最大为1M')
    //       reject(new Error('纯文本文件最大为1M'))
    //       return
    //     }
    //     if (type === 'epub' && file.size / 1024 / 1024 > 20) {
    //       this.$message.error('电子书文件最大20M')
    //       reject(new Error('电子书文件最大20M'))
    //       return
    //     }
    //     if ((type === 'mp3' || type === 'acc' || type === 'mp4' || type === 'mpg' || type === 'avi' || type === 'wmv') && file.size / 1024 / 1024 > 200) {
    //       this.$message.error('音视频文件最大为200M')
    //       reject(new Error('音视频文件最大为200M'))
    //       return
    //     }
    //     if (file.size / 1024 / 1024 > 200) {
    //       this.$message.error('文件最大为200M')
    //       reject(new Error('文件最大为200M'))
    //       return
    //     }
    //     if (type !== 'mp3' && type !== 'acc' && type !== 'mp4' && type !== 'mpg' && type !== 'avi' && type !== 'wmv' &&
    //         type !== 'epub' && type !== 'txt' && type !== 'jpg' && type !== 'png' && type !== 'gif' && type !== 'jpeg' &&
    //         type !== 'pdf' && type !== 'doc' && type !== 'docx' && type !== 'ppt' && type !== 'pptx' && type !== 'xls' &&
    //         type !== 'xlsx' && type !== 'exe' && type !== 'rar' && type !== 'zip') {
    //       this.$message.error('文件格式错误')
    //       reject(new Error('文件格式错误'))
    //       return
    //     }
    //     resolve()
    //   })
    // },
    beforeImgUpload (file) {
      return new Promise(async (resolve, reject) => {
        const type = file.name.split('.')[file.name.split('.').length - 1].toLowerCase()
        if ((type === 'pdf' || type === 'doc' || type === 'docx' || type === 'ppt' || type === 'pptx' || type === 'xls' || type === 'xlsx') && file.size / 1024 / 1024 > 200) {
          this.$message.error('文档最大为200M')
          reject(new Error('文档最大为200M'))
          return
        }
        if ((type === 'jpg' || type === 'png' || type === 'gif' || type === 'jpeg') && file.size / 1024 / 1024 > 5) {
          this.$message.error('图片文件最大为5M')
          reject(new Error('图片文件最大为5M'))
          return
        }
        if (type === 'txt' && file.size / 1024 / 1024 > 2) {
          this.$message.error('纯文本文件最大为2M')
          reject(new Error('纯文本文件最大为2M'))
          return
        }
        // if (type === 'epub' && file.size / 1024 / 1024 > 20) {
        //   this.$message.error('电子书文件最大20M')
        //   reject(new Error('电子书文件最大20M'))
        //   return
        // }
        // if ((type === 'mp3' || type === 'acc' || type === 'mp4' || type === 'mpg' || type === 'avi' || type === 'wmv') && file.size / 1024 / 1024 > 200) {
        //   this.$message.error('音视频文件最大为200M')
        //   reject(new Error('音视频文件最大为200M'))
        //   return
        // }
        if (file.size / 1024 / 1024 > 200) {
          this.$message.error('文件最大为200M')
          reject(new Error('文件最大为200M'))
          return
        }
        if (type !== 'txt' && type !== 'jpg' && type !== 'png' && type !== 'gif' && type !== 'jpeg' &&
            type !== 'pdf' && type !== 'doc' && type !== 'docx' && type !== 'ppt' && type !== 'pptx' && type !== 'xls' &&
            type !== 'xlsx' && type !== 'rar' && type !== 'zip') {
          this.$message.error('文件格式错误')
          reject(new Error('文件格式错误'))
          return
        }
        resolve()
      })
    },
    async getList () {
      this.tableData = []
      await this.$store.dispatch('fileSelect', {
        pageNum: this.currentPage,
        pageSize: this.pageNum,
        username: this.isAdmin ? '' : this.$store.state.accountname
      }).then((res) => {
        if (res && res.list && res.list.length > 0) {
          if (this.$store.state.userid && this.$store.state.userid.split(',').length === 7) {
            this.tableData = res.list.map((item, i) => ({
              ...item,
              filetype: item.filename ? ('.' + item.filename.split('.')[item.filename.split('.').length - 1]) : '未知类型'
            }))
          } else {
            this.tableData = res.list.map(item => ({
              ...item,
              filetype: item.filename ? ('.' + item.filename.split('.')[item.filename.split('.').length - 1]) : '未知类型'
            }))
          }
          this.totalCount = res.total
        }
      })
    },
    async delSure () {
      await this.$store.dispatch('fileDel', {
        groupId: this.inifoId
      }).then((res) => {
        this.deleteDialog = false
        this.getList()
      })
    }
  },
  created () {
    let xi = this.$store.state.userid.split(',')
    if (xi && xi.length === 7) {
      this.isAdmin = true
    } else {
      this.isAdmin = false
    }
    this.getList()
  }
}
</script>

<style lang="scss">
.manage-three {
  text-align: left;
  .manage-add {
    height: 40px;
    line-height: 40px;
    border-bottom: 1px solid #efefef;
    padding-left: 20px;
    box-sizing: border-box;
  }
  .tabs {
    padding: 20px;
    .el-table th:first-child {
      border-top-left-radius: 0;
      border-bottom-left-radius: 0;
    }
    th {
      background: #f5f7fa;
    }
  }
  .el-form {
    .el-input {
      width: 90%;
    }
  }
  .el-pagination {
    margin-top: 20px;
    text-align: right;
  }
  .fileicon {
    .el-upload {
      display: none;
    }
    .el-upload__tip {
      margin-top: 0;
    }
  }
}
</style>



