import axios from './axios'

// 添加用户
export function userAdd (data) {
  return axios.post('/user/create', data).then((res) => res.data)
}
// 删除用户
export function userDel (data) {
  return axios.delete('/user/delete', {params: data}).then((res) => res.data)
}
// 查询用户列表
export function userGet (data) {
  return axios.get('/user/select', {params: data}).then((res) => res.data)
}
// 修改用户
export function userChange (data) {
  return axios.post('/user/update', data).then((res) => res.data)
}
// 用户登录
export function userLogin (data) {  return axios.post('/userLogin', data).then((res) => res.data)
}

export function qn (data) {
  return axios.get('/api/qn').then(res => res.data)
}



