<template>
  <div class="productInfo-detail">
    <!-- 通知详情-->
    <div class="detail-box">
      <div class="return">当前位置：<span class="prevlevel" @click="toPrev">生产信息</span> / <span class="now">生产详情</span></div>
      <div class="artical-detail">
        <h2>{{artical.title}}</h2>
        <p class="des">
          <span class="author">作者：{{artical.author}}</span>
          <span class="date">日期：{{artical.date}}</span>
        </p>
        <div class="img-box" v-if="artical.picture && artical.picture.length > 0">
          <img :src="artical.picture" alt="" />
        </div>
        <div class="contents" v-html="artical.content"></div>
        <div class="filePath" v-if="artical.fufile">
          <p>
            <span>附件：</span>
            <a :href="artical.fufile" download style="color: #409EFF;" Content-Disposition="attachment">{{artical.fufilename}}</a>
          </p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
// import {dateFormat} from '@/filters/index'
export default {
  name: 'productInfo-detail',
  data () {
    return {
      artical: {
        title: '',
        date: '',
        author: '',
        content: '',
        fufile: '',
        fufilename: ''
      }
    }
  },
  methods: {
    toPrev () {
      this.$router.push('/productInfo')
    }
  },
  created () {
    let item = JSON.parse(window.sessionStorage.productDetail || {titleofarticle: '', time: '', authorofthzarticle: '', contentofthearticle: '', picture: '', fufile: ''})
    if (!item) return
    this.artical = {
      title: item.titleofarticle,
      date: item.time,
      author: item.authorofthzarticle,
      content: item.contentofthearticle,
      picture: item.picture,
      fufile: item.fufile,
      fufilename: item.fufilename
    }
  }
}
</script>

<style lang="scss">
.productInfo-detail {
  padding: 20px 0;
  .img-box {
    text-align: center;
    > img {
      margin: 20px auto;
      max-width: 1160px;
    }
  }
}
</style>



