import Vue from 'vue'
import Vuex from 'vuex'
import artical from './module/articalApi'
import account from './module/accountApi'
import files from './module/fileApi'

Vue.use(Vuex)

const state = {
    indexActive: window.sessionStorage.CAIHONGINDEX || 0,
    username: window.sessionStorage.USERNAME || '',
    userid: window.sessionStorage.USERID || '',
    accountname: window.sessionStorage.ACCOUTNAME
}

const actions = {

}

const mutations = {
    CHANGEINDEX (state, data) {
        window.sessionStorage.CAIHONGINDEX = ''
        state.indexActive = data
        window.sessionStorage.CAIHONGINDEX = data
    },
    USERINFO (state, data) {
        window.sessionStorage.USERNAME = ''
        window.sessionStorage.USERID = ''
        window.sessionStorage.ACCOUTNAME = ''
        state.username = data.name
        state.userid = data.userid
        state.accountname = data.accountname
        window.sessionStorage.USERNAME = data.name
        window.sessionStorage.USERID = data.userid
        window.sessionStorage.ACCOUTNAME = data.accountname
    }
}

export default new Vuex.Store({
  state,
  actions,
  mutations,
  modules: {
    artical,
    account,
    files
  }
})



