<template>
  <div class="manage-one">
    <!-- 账号管理 -->
    <div class="manage-add">
      <el-button type="primary" size="mini" @click="addNew">新增</el-button>
      <el-button type="success" size="mini" @click="getList">刷新</el-button>
    </div>
    <div class="tabs">
      <el-table
        :data="tableData"
        style="width: 100%">
        <el-table-column
          prop="name"
          label="姓名"
          width="180">
        </el-table-column>
        <el-table-column
          prop="username"
          label="用户名"
          width="180">
        </el-table-column>
        <!-- <el-table-column
          prop="department"
          label="部门">
        </el-table-column> -->
        <el-table-column
          prop="respons"
          label="模块权限">
        </el-table-column>
        <el-table-column label="操作" width="150">
          <template slot-scope="scope">
            <el-button
              size="mini"
              @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
            <el-button
              size="mini"
              type="danger"
              @click="handleDelete(scope.$index, scope.row)">删除</el-button>
          </template>
        </el-table-column>
      </el-table>
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="currentPage"
        :page-sizes="[10, 20, 30, 40, 50]"
        :page-size="pageNum"
        layout="total, sizes, prev, pager, next, jumper"
        :total="totalCount">
      </el-pagination>
    </div>
    <!-- 新增或编辑 -->
    <el-dialog
      :title="isChange ? '编辑账号' : '新增账号'"
      :close-on-click-modal="false"
      :visible.sync="addNewDialog"
      width="40%">
      <el-form :model="ruleForm" status-icon :rules="rules2" ref="ruleForm" label-width="110px" class="demo-ruleForm">
        <el-form-item label="姓名" prop="name">
          <el-input type="text" v-model="ruleForm.name" placeholder="请输入姓名" :disabled="isChange"></el-input>
        </el-form-item>
        <!-- <el-form-item label="所属部门" prop="department">
          <el-input type="text" v-model="ruleForm.department" placeholder="请输入部门名称"></el-input>
        </el-form-item> -->
        <el-form-item label="用户名" prop="username">
          <el-input type="text" v-model="ruleForm.username" placeholder="请输入用户名"></el-input>
        </el-form-item>
        <el-form-item label="密码" prop="password" v-if="!isChange">
          <el-input type="text" v-model="ruleForm.password" placeholder="请输入密码"></el-input>
        </el-form-item>
        <el-form-item label="确认密码" prop="repeatPassword" v-if="!isChange">
          <el-input type="text" v-model="ruleForm.repeatPassword" placeholder="请再次输入密码"></el-input>
        </el-form-item>
        <el-form-item label="模块权限" prop="userid">
          <el-checkbox-group v-model="ruleForm.userid">
            <el-checkbox label="公司新闻"></el-checkbox>
            <el-checkbox label="通知通告"></el-checkbox>
            <el-checkbox label="生产信息"></el-checkbox>
            <el-checkbox label="党建专栏"></el-checkbox>
            <el-checkbox label="企业文化"></el-checkbox>
            <el-checkbox label="规章制度"></el-checkbox>
            <el-checkbox label="工团建设"></el-checkbox>
          </el-checkbox-group>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="resetForm('ruleForm')">取 消</el-button>
        <el-button type="primary" @click="submitForm('ruleForm')">确 定</el-button>
      </span>
    </el-dialog>
    <!-- 删除弹框 -->
    <el-dialog
      title="删除"
      :visible.sync="deleteDialog"
      :close-on-click-modal="false"
      width="30%">
      <span>确定删除该账号吗？</span>
      <span slot="footer" class="dialog-footer">
        <el-button @click="deleteDialog = false">取 消</el-button>
        <el-button type="primary" @click="delSure">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: 'manage-one',
  data () {
    var validatePass = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请再次输入密码'))
      } else if (value !== this.ruleForm.password) {
        callback(new Error('两次输入密码不一致!'))
      } else {
        callback()
      }
    }
    return {
      tableData: [],
      ruleForm: {
        name: '',
        // department: '',
        username: '',
        password: '',
        repeatPassword: '',
        userid: ['公司新闻']
      },
      rules2: {
        name: [
          { required: true, trigger: 'blur', message: '请输入用户姓名' }
        ],
        // department: [
        //   { required: true, trigger: 'blur', message: '请输入部门名称' }
        // ],
        username: [
          { required: true, trigger: 'blur', message: '请输入用户名称' }
        ],
        password: [
          { required: true, trigger: 'blur', message: '请输入密码' }
        ],
        repeatPassword: [
          { required: true, validator: validatePass, trigger: 'blur' }
        ],
        userid: [
          // { required: true, type: 'array', trigger: 'change', message: '请至少选择一个模块权限' }
        ]
      },
      addNewDialog: false,
      isChange: false,
      deleteDialog: false,
      currentPage: 0,
      pageNum: 10,
      totalCount: 0,
      keyword: '',
      infoId: ''
    }
  },
  methods: {
    handleEdit (index, row) {
      this.infoId = row.id
      this.isChange = true
      this.addNewDialog = true
      this.ruleForm.name = row.name
      this.ruleForm.password = row.password
      // this.ruleForm.department = row.department
      this.ruleForm.username = row.username
      let arr = row.userid.split(',')
      arr.forEach((item, i) => {
        if (Number(item) === 1) {
          arr[i] = '公司新闻'
        } else if (Number(item) === 2) {
          arr[i] = '通知通告'
        } else if (Number(item) === 3) {
          arr[i] = '生产信息'
        } else if (Number(item) === 4) {
          arr[i] = '党建专栏'
        } else if (Number(item) === 5) {
          arr[i] = '企业文化'
        } else if (Number(item) === 6) {
          arr[i] = '规章制度'
        } else if (Number(item) === 7) {
          arr[i] = '工团建设'
        }
      })
      this.ruleForm.userid = arr
    },
    handleDelete (index, row) {
      this.infoId = row.id
      this.deleteDialog = true
    },
    addNew () {
      this.isChange = false
      this.addNewDialog = true
      this.ruleForm = {
        name: '',
        // department: '',
        username: '',
        password: '',
        repeatPassword: '',
        userid: []
      }
    },
    submitForm (formName) {
      this.$refs[formName].validate(async (valid) => {
        if (valid) {
          let arr = []
          this.ruleForm.userid.forEach(item => {
            if (item === '公司新闻') {
              arr.push(1)
            } else if (item === '通知通告') {
              arr.push(2)
            } else if (item === '生产信息') {
              arr.push(3)
            } else if (item === '党建专栏') {
              arr.push(4)
            } else if (item === '企业文化') {
              arr.push(5)
            } else if (item === '规章制度') {
              arr.push(6)
            } else if (item === '工团建设') {
              arr.push(7)
            }
          })
          let userids = arr.join(',')
          if (this.isChange) {
            await this.$store.dispatch('userChange', {
              // name: this.ruleForm.name,
              // department: this.ruleForm.department,
              username: this.ruleForm.username,
              password: this.ruleForm.password,
              userid: userids,
              id: this.infoId
            })
          } else {
            await this.$store.dispatch('userAdd', {
              name: this.ruleForm.name,
              // department: this.ruleForm.department,
              username: this.ruleForm.username,
              password: this.ruleForm.password,
              userid: userids
            })
          }
          this.getList()
          this.$refs[formName].resetFields()
        } else {
          return false
        }
        this.addNewDialog = false
      })
    },
    resetForm (formName) {
      this.$refs[formName].resetFields()
      this.addNewDialog = false
    },
    handleSizeChange (val) {
      this.pageNum = val
      this.getList()
    },
    handleCurrentChange (val) {
      this.currentPage = val
      this.getList()
    },
    async getList () {
      await this.$store.dispatch('userGet', {
        pageNum: this.currentPage,
        pageSize: this.pageNum,
        username: this.keyword
      }).then((res) => {
        this.tableData = []
        if (res && res.list && res.list.length > 0) {
          this.tableData = res.list.map((item, i) => ({
            ...item,
            respons: this.filArr(item.userid).length > 0 ? this.filArr(item.userid) : '暂无权限'
          }))
          this.totalCount = res.total
        }
      })
    },
    async delSure () {
      this.deleteDialog = false
      await this.$store.dispatch('userDel', {
        groupId: this.infoId
      })
      this.getList()
    },
    filArr (item) {
      if (item) {
        let arr = item.split(',')
        arr.forEach((items, i) => {
          if (Number(items) === 1) {
            arr[i] = '公司新闻'
          } else if (Number(items) === 2) {
            arr[i] = '通知通告'
          } else if (Number(items) === 3) {
            arr[i] = '生产信息'
          } else if (Number(items) === 4) {
            arr[i] = '党建专栏'
          } else if (Number(items) === 5) {
            arr[i] = '企业文化'
          } else if (Number(items) === 6) {
            arr[i] = '规章制度'
          } else if (Number(items) === 7) {
            arr[i] = '工团建设'
          }
        })
        let arrs = arr.join('、')
        return arrs
      } else return []
    }
  },
  created () {
    this.getList()
  }
}
</script>

<style lang="scss">
.manage-one {
  text-align: left;
  .manage-add {
    height: 40px;
    line-height: 40px;
    border-bottom: 1px solid #efefef;
    padding-left: 20px;
    box-sizing: border-box;
  }
  .tabs {
    padding: 20px;
    .el-table th:first-child {
      border-top-left-radius: 0;
      border-bottom-left-radius: 0;
    }
    th {
      background: #f5f7fa;
    }
  }
  .el-form {
    .el-input {
      width: 90%;
    }
  }
  .el-pagination {
    margin-top: 20px;
    text-align: right;
  }
  .el-checkbox {
    margin-right: 30px;
    margin-left: 0 !important;
  }
}
</style>



