<template>
  <div class="index">
    <div class="content clearfix">
      <div class="content-left">
        <div class="top-title">公司新闻</div>
        <div class="news">
          <div class="one-p" v-for="(item, i) in newList" :key="i">
            <p class="first-line" @click="toDetail(1, item)">
              <span v-if="item && item.titleofarticle && item.titleofarticle.length < 49">{{item.titleofarticle}}</span>
              <span v-else>{{item.titleofarticle.substring(0, 48) + '...'}}</span>
            </p>
            <p class="date">
              <span>发布者：{{item.authorofthzarticle}}</span>
              <span>发布时间：{{item.time}}</span>
            </p>
          </div>
        </div>
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :page-size="pageNum"
          background
          :current-page="currentPage"
          layout="total, sizes, prev, pager, next, jumper"
          :total="totalCount">
        </el-pagination>
      </div>
      <div class="content-right">
        <div class="img-news">
          <div class="top-title">图片新闻</div>
          <el-carousel :interval="3000" arrow="always" indicator-position="none">
            <el-carousel-item v-for="(item, i) in imgList" :key="i">
              <img :src="item.picture" alt="" @click="toDetail(item.columntype, item)" @error="imgError(item)" />
              <div class="model" v-if="item.titleofarticle.length < 21" @click="toDetail(item.columntype, item)">{{item.titleofarticle}}</div>
              <div class="model" v-else @click="toDetail(item.columntype, item)">{{item.titleofarticle.substring(0, 20) + '...'}}</div>
            </el-carousel-item>
          </el-carousel>
        </div>
        <div class="notice-news">
          <div class="top-title">通知公告</div>
          <div class="notices">
            <p v-for="(item, i) in notices" :key="i" @click="toDetail(2, item)">
              <span v-if="item.titleofarticle && item.titleofarticle.length < 20">{{item.titleofarticle}}</span>
              <span v-else>{{item.titleofarticle.substring(0, 19) + '...'}}</span>
            </p>
          </div>
        </div>
        <div class="notice-news product-news">
          <div class="top-title">生产信息</div>
          <div class="notices">
            <p v-for="(item, i) in products" :key="i" @click="toDetail(3, item)">
              <span v-if="item.titleofarticle && item.titleofarticle.length < 20">{{item.titleofarticle}}</span>
              <span v-else>{{item.titleofarticle.substring(0, 19) + '...'}}</span>
            </p>
          </div>
        </div>
        <div class="notice-news dang-news">
          <div class="top-title">党建专栏</div>
          <div class="notices">
            <p v-for="(item, i) in dangs" :key="i" @click="toDetail(4, item)">
              <span v-if="item.titleofarticle && item.titleofarticle.length < 20">{{item.titleofarticle}}</span>
              <span v-else>{{item.titleofarticle.substring(0, 19) + '...'}}</span>
            </p>
          </div>
        </div>
        <div class="notice-news rule">
          <div class="top-title">规章制度</div>
          <div class="notices">
            <p v-for="(item, i) in rules" :key="i" @click="toDetail(6, item)">
              <span v-if="item.titleofarticle && item.titleofarticle.length < 20">{{item.titleofarticle}}</span>
              <span v-else>{{item.titleofarticle.substring(0, 19) + '...'}}</span>
            </p>
          </div>
        </div>
        <div class="notice-news download-file">
          <div class="top-title">文件下载</div>
          <div class="notices">
            <p v-for="(item, i) in files" :key="i" class="clearfix">
              <span v-if="item.fileheadings && item.fileheadings.length < 16" style="float: left;width: calc(100% - 70px);">{{item.fileheadings}}</span>
              <span v-else style="float: left;">{{item.fileheadings.substring(0, 15) + '...'}}</span>
              <!-- <el-button type="text" style="float: right;padding: 7px;">下载</el-button> -->
              <span>
                <a :href="item.fileenclosure" download class="download-a" style="float: right;color: #409EFF;">下载</a>
              </span>
            </p>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import none from '@/assets/image/none.png'
export default {
  name: 'index',
  data () {
    return {
      newList: [],
      notices: [],
      products: [],
      dangs: [],
      rules: [],
      files: [],
      pageNum: 10,
      totalCount: 0,
      none,
      currentPage: 0,
      imgList: [
        {picture: '', titleofarticle: ''}
      ]
    }
  },
  methods: {
    handleSizeChange (val) {
      this.getList(val, this.currentPage, 1)
    },
    handleCurrentChange (val) {
      this.getList(this.pageNum, val, 1)
    },
    toDetail (val, item) {
      console.log(val)
      console.log(item)
      this.$store.commit('CHANGEINDEX', val)
      if (Number(val) === 1) {
        window.sessionStorage.newDetail = JSON.stringify(item)
        this.$router.push('/newsDetail')
      } else if (Number(val) === 2) {
        window.sessionStorage.noticeDetail = JSON.stringify(item)
        this.$router.push('/noticeDetail')
      } else if (Number(val) === 3) {
        window.sessionStorage.productDetail = JSON.stringify(item)
        this.$router.push('/productDetail')
      } else if (Number(val) === 4) {
        window.sessionStorage.partDetail = JSON.stringify(item)
        this.$router.push('/partyBuildDetail')
      } else if (Number(val) === 5) {
        window.sessionStorage.cultureDetail = JSON.stringify(item)
        this.$router.push('/cultureDetail')
      } else if (Number(val) === 6) {
        window.sessionStorage.ruleDetail = JSON.stringify(item)
        this.$router.push('/rulesDetail')
      } else if (Number(val) === 7) {
        window.sessionStorage.ruleDetail = JSON.stringify(item)
        this.$router.push('/partyBuildDetail')
      }
    },
    async getList (size, now, types) {
      this.tableData = []
      await this.$store.dispatch('articleGet', {
        pageSize: size,
        pageNum: now,
        type: types
      }).then((res) => {
        if (res && res.list && res.list.length > 0) {
          this.tableData = (res && res.list && res.list.length > 0) ? res.list : []
          if (res && res.list && res.list.length > 0) {
            if (Number(types) === 1) {
              this.newList = res.list
              this.totalCount = res.total
            } else if (Number(types) === 2) {
              this.notices = res.list.slice(-3)
            } else if (Number(types) === 3) {
              this.products = res.list.slice(-3)
            } else if (Number(types) === 4) {
              this.dangs = res.list.slice(-2)
            } else if (Number(types) === 6) {
              this.rules = res.list.slice(-3)
            } else if (!types) {
              this.imgList = res.list.filter(item => (item.picture && item.picture.length > 1))
            }
          }
        }
      })
    },
    async getFile () {
      this.files = []
      await this.$store.dispatch('fileSelect', {
        pageNum: 0,
        pageSize: 3
      }).then((res) => {
        if (res && res.list && res.list.length > 0) {
          this.files = res.list
        }
      })
    },
    async getAll () {
      await this.getList(this.pageNum, this.currentPage, 1)
      await this.getList(this.pageNum, 0, 2)
      await this.getList(this.pageNum, 0, 3)
      await this.getList(this.pageNum, 0, 4)
      await this.getList(this.pageNum, 0, 6)
      await this.getList(this.pageNum, 0, 4)
      await this.getList(this.pageNum, 0)
      await this.getFile()
    },
    imgError (val) {
      this.$set(val, 'picture', this.none)
    }
  },
  created () {
    this.getAll()
  }
}
</script>

<style lang="scss" scoped>
.index {
  padding: 20px 0;
  > .content {
    box-sizing: border-box;
    padding: 0 20px;
    text-align: left;
    > .content-left {
      width: 68%;
      height: 100%;
      float: left;
      .top-title {
        background: #2CADFF;
      }
      > .news {
        > .one-p {
          height: 80px;
          border-bottom: 1px dashed #999;
          padding: 0 20px;
          box-sizing: border-box;
          border-right: 1px solid #ccc;
          cursor: pointer;
          > p {
            height: 40px;
            line-height: 40px;
          }
          > .first-line {
            > span:first-child {
              font-weight: bold;
            }
          }
          > .first-line:hover {
            > span:first-child {
              color: #2CADFF;
            }
          }
          > .date {
            > span {
              color: #333;
              font-size: 12px;
              // float: right;
              // margin-left: 20px;
            }
            > span:first-child {
              float: left;
            }
            > span:last-child {
              float: right;
            }
          }
        }
      }
      .el-pagination {
        margin-top: 40px;
        text-align: right;
      }
    }
    > .content-right {
      width: 30%;
      height: 100%;
      float: right;
      > div {
        border: 1px solid #ccc;
      }
      > .img-news {
        position: relative;
        height: 300px;
        cursor: pointer;
        .el-carousel {
          img {
            width: 346px;
            height: 230px;
          }
          .model {
            position: absolute;
            width: 100%;
            padding: 6px 0;
            margin-top: -5px;
            text-align: center;
            background: rgba(0,0,0,0.5);
            color: #fff;
          }
          .model:hover {
            color: blue;
          }
        }
        > .top-title {
          background: #4D80E6;
        }
      }
      > .notice-news {
        margin-top: 10px;
        .top-title {
          background: #222C48;
        }
        .notices {
          padding: 10px 20px;
          > p {
            // height: 30px;
            line-height: 30px;
            cursor: pointer;
          }
          > p:hover {
            color: #2CADFF;
          }
        }
      }
      > .product-news {
        .top-title {
          background: #0D83E9;
        }
      }
      > .dang-news {
        .top-title {
          background: #CC0000;
        }
      }
      > .rule {
        .top-title {
          background: #22508C;
        }
      }
      > .download-file {
        .top-title {
          background: #79ACED;
        }
        .notices {
          > p:hover {
            color: #333;
          }
        }
      }
    }
    .top-title {
      height: 40px;
      line-height: 40px;
      box-sizing: border-box;
      padding-left: 20px;
      color: #fff;
    }
  }
}
</style>



