import Vue from 'vue'
import Router from 'vue-router'
import home from '@/pages/home'
import index from '@/pages/index/index'
import news from '@/pages/news/index'
import newsDetail from '@/pages/news/detail'
import notice from '@/pages/notice/index'
import noticeDetail from '@/pages/notice/detail'
import productInfo from '@/pages/productInfo/index'
import productDetail from '@/pages/productInfo/detail'
import partyBuild from '@/pages/partyBuild/index'
import partyBuildDetail from '@/pages/partyBuild/detail'
import download from '@/pages/download/index'
import manage from '@/pages/manage/index'
import manageAccount  from '@/pages/manage/childrens/one'
import manageArticle  from '@/pages/manage/childrens/two'
import manageFile  from '@/pages/manage/childrens/three'
import culture from '@/pages/culture/index'
import cultureDetail from '@/pages/culture/detail'
import rules from '@/pages/rules/index'
import rulesDetail from '@/pages/rules/detail'
Vue.use(Router)
export default new Router({
  routes: [
    {
      path: '/',
      name: 'home',
      component: home,
      redirect: '/index',
      children: [
        { path: '/index', component: index, name: '主页', meta: { page: 0 } },
        { path: '/culture', component: culture, name: '企业文化', meta: { page: 1 } },
        { path: '/cultureDetail', component: cultureDetail, name: '企业文化详情', meta: { page: 1 } },
        { path: '/rules', component: rules, name: '规章制度', meta: { page: 2 } },
        { path: '/rulesDetail', component: rulesDetail, name: '规章制度详情', meta: { page: 2 } },
        { path: '/news', component: news, name: '新闻', meta: { page: 3 } },
        { path: '/newsDetail', component: newsDetail, name: '新闻详情', meta: { page: 3 } },
        { path: '/notice', component: notice, name: '通知', meta: { page: 4 } },
        { path: '/noticeDetail', component: noticeDetail, name: '通知详情', meta: { page: 4 } },
        { path: '/productInfo', component: productInfo, name: '生产中心', meta: { page: 5 } },
        { path: '/productDetail', component: productDetail, name: '生产详情', meta: { page: 5 } },
        { path: '/partyBuild', component: partyBuild, name: '党建专栏', meta: { page: 6 } },
        { path: '/partyBuildDetail', component: partyBuildDetail, name: '党建详情', meta: { page: 6 } },
        { path: '/download', component: download, name: '下载中心', meta: { page: 7 } },
        {
          path: '/manage',
          component: manage,
          name: '后台管理',
          redirect: '/manage/manageAccount',
          meta: { page: 8 },
          children: [
            { path: '/manage/manageAccount', component: manageAccount, name: '账号管理', meta: { page: 8, small: 1 } },
            { path: '/manage/manageArticle', component: manageArticle, name: '文档管理', meta: { page: 8, small: 2 } },
            { path: '/manage/manageFile', component: manageFile, name: '文件管理', meta: { page: 8, small: 3 } }
          ]
        },
      ]
    }
  ]
})


