import service from '@/api/all'
import { notice, success } from '@/api/index'
const server = service.accountApi
const state = {
}
const actions = {
  // 新增账户
  async userAdd ({commit}, data) {
    const res = await server.userAdd(data)
    if (res.code === '00') return success('新增成功')
    else notice('新增失败')
  },
  // 删除账号
  async userDel ({commit}, data) {
    const res = await server.userDel(data)
    if (res && res.code === '00') return success('删除成功')
    if (res && res.code === '02') {}
    else notice('删除失败')
  },
  // 查询账号
  async userGet ({commit}, data) {
    const res = await server.userGet(data)
    if (res && res.code === '00') return res.data
    if (res && res.code === '02') {}
    else notice('查询失败')
  },
  // 修改账号
  async userChange ({commit}, data) {
    const res = await server.userChange(data)
    if (res && res.code === '00') return success('修改成功')
    if (res && res.code === '02') {}
    else notice('修改失败')
  },
  // 用户登录
  async userLogin ({commit}, data) {
    const res = await server.userLogin(data)
    if (res && res.code === '00') return res
    else if (res && res.code === '01') return res
    if (res && res.code === '02') {}
    else notice('登录失败,请联系管理员')
  },
  async qn ({commit}, data) {
    const res = await server.qn(data)
    return res
    // if (res && res.code === '00') return res
    // else if (res && res.code === '01') return res
    // else notice(res || '登录失败,请联系管理员')
  }
}

const mutations = {
}

export default ({
  state,
  actions,
  mutations
})



