import service from '@/api/all'
import { notice, success } from '@/api/index'
const server = service.fileApi
const state = {
}
const actions = {
  async fileSelect ({commit}, data) {
    const res = await server.fileSelect(data)
    if (res && res.code === '00') return res.data
    if (res && res.code === '02') {}
    else notice('查询失败')
  },
  async fileDel ({commit}, data) {
    const res = await server.fileDel(data)
    if (res && res.code === '00') return success('删除成功')
    if (res && res.code === '02') {}
    else notice('删除失败')
  },
  async fileAdd ({commit}, data) {
    const res = await server.fileAdd(data)
    if (res && res.code === '00') return res.data
    if (res && res.code === '02') {}
    else notice('添加失败')
  }
}

const mutations = {
}

export default ({
  state,
  actions,
  mutations
})



