<template>
    <div class="hello">
        <div id="editor">
        </div>
    </div>
</template>

<script>
import E from 'wangeditor'
export default {
  data () {
    return {
      content: '',
      edit: ''
    }
  },
  props: ['mode'],
  methods: {
    clea () {
      this.edit.txt.clear()
    },
    change () {
      this.$nextTick(() => {
        this.clea()
        this.edit.txt.html(this.mode)
      })
    }
  },
  created () {
    if (this.mode) {
      this.change()
    }
  },
  mounted () {
    let _this = this
    var editor = this.edit = new E('#editor')
    editor.customConfig.uploadImgShowBase64 = true
    // 默认alert弹出报错，这里可以修改
    editor.customConfig.customAlert = function (info) {
      // info 是需要提示的内容
      alert('自定义提示：' + info)
    }
    editor.customConfig.menus = [ 
      'head', // 标题 
      'bold', // 粗体 
      'fontSize', // 字号 
      'fontName', // 字体 
      'italic', // 斜体 
      'underline', // 下划线 
      'strikeThrough', // 删除线 
      'foreColor', // 文字颜色 
      'backColor', // 背景颜色
      'list', // 列表 
      'justify', // 对齐方式 
      'quote', // 引用 
      'image', // 插入图片 
      'table', // 表格 
      'code', // 插入代码 
      'undo', // 撤销 
      'redo' // 重复 
    ] 
    editor.customConfig.onchange = function (info) {
      _this.content = info
      _this.$emit('cont', info)
    }
    editor.create()
    // 修改内容
    // editor.txt.html('<p>用 JS 设置的内容</p>')
    // 清空
    // editor.txt.clear()
  }
}
</script>



