<template>
  <div class="notice">
    <!-- 通知公告 -->
    <div class="new-content">
      <div class="top-title">
        <span>通知通告</span>
        <div style="display: inline-block;float: right;margin-right: 20px;">
          <el-input
            placeholder="请输入内容"
            v-model="keyword">
            <i slot="suffix" class="el-input__icon el-icon-search" @click="getList"></i>
          </el-input>
        </div>
      </div>
      <div v-for="(item, i) in newList" :key="i" class="articals">
        <div class="clearfix">
          <p>
            <span v-if="item.titleofarticle && item.titleofarticle.length < 66" @click="toDetail(item)">{{item.titleofarticle}}</span>
            <span v-else @click="toDetail(item)">{{item.titleofarticle.substring(0, 65) + '...'}}</span>
          </p>
          <p>
            <span>发布人：{{item.authorofthzarticle}}</span>
            <span>发布时间：{{item.time}}</span>
          </p>
        </div>
      </div>
      <el-pagination
        v-if="newList && newList.length > 0"
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="currentPage"
        :page-size="pageNum"
        background
        layout="total, sizes, prev, pager, next, jumper"
        :total="totalCount">
      </el-pagination>
    </div>
  </div>
</template>

<script>
// import {dateFormat} from '@/filters/index'
export default {
  name: 'notice',
  data () {
    return {
      newList: [],
      currentPage: 0,
      totalCount: 0,
      keyword: '',
      pageNum: 10
    }
  },
  watch: {
    keyword () {
      this.getList()
    }
  },
  methods: {
    handleSizeChange (val) {
      this.pageNum = val
      this.getList()
    },
    handleCurrentChange (val) {
      this.currentPage = val
      this.getList()
    },
    toDetail (item) {
      window.sessionStorage.noticeDetail = JSON.stringify(item)
      this.$router.push('/noticeDetail')
    },
    async getList () {
      this.newList = []
      await this.$store.dispatch('articleGet', {
        pageSize: this.pageNum,
        pageNum: this.currentPage,
        type: 2,
        titleofarticle: this.keyword
      }).then((res) => {
        if (res && res.list && res.list.length > 0) {
          this.newList = res.list
          this.totalCount = res.total
        }
      })
    }
  },
  created () {
    this.getList()
  }
}
</script>

<style lang="scss">
.notice {
  padding: 20px 0;
  > .new-content {
    .top-title {
      background: #222C48;
    }
  }
}
</style>



