<template>
  <div class="partyBuild">
    <!-- party -->
    <div class="new-content clearfix">
      <!-- 左边 -->
      <div class="clear-left">
        <div class="top-title">
          <span>党建专栏</span>
          <div style="display: inline-block;float: right;margin-right: 20px;">
            <el-input
              placeholder="请输入内容"
              v-model="keyword">
              <i slot="suffix" class="el-input__icon el-icon-search" @click="getList"></i>
            </el-input>
          </div>
        </div>
        <div class="clear-content">
          <div v-for="(item, i) in newList" :key="i" class="articals">
            <div class="clearfix">
              <p>
                <span v-if="item.titleofarticle && item.titleofarticle.length < 66" @click="toDetail(item)">{{item.titleofarticle}}</span>
                <span v-else @click="toDetail(item)">{{item.titleofarticle.substring(0, 65) + '...'}}</span>
              </p>
              <p>
                <span>发布人：{{item.authorofthzarticle}}</span>
                <span>发布时间：{{item.time}}</span>
              </p>
            </div>
          </div>
        </div>
        <el-pagination
          v-if="newList && newList.length > 0"
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="currentPage"
          :page-size="pageNum"
          background
          layout="total, sizes, prev, pager, next, jumper"
          :total="totalCount">
        </el-pagination>
      </div>
      <!-- 右边 -->
      <div class="clear-right">
        <div class="top-title">
          <span>工团建设</span>
          <div style="display: inline-block;float: right;margin-right: 20px;">
            <el-input
              placeholder="请输入内容"
              v-model="unionkeyword">
              <i slot="suffix" class="el-input__icon el-icon-search" @click="getUnionList"></i>
            </el-input>
          </div>
        </div>
        <div class="clear-content">
          <div v-for="(item, i) in unionList" :key="i" class="articals">
            <div class="clearfix">
              <p>
                <span v-if="item.titleofarticle && item.titleofarticle.length < 66" @click="toDetail(item)">{{item.titleofarticle}}</span>
                <span v-else @click="toDetail(item)">{{item.titleofarticle.substring(0, 65) + '...'}}</span>
              </p>
              <p>
                <span>发布人：{{item.authorofthzarticle}}</span>
                <span>发布时间：{{item.time}}</span>
              </p>
            </div>
          </div>
        </div>
        <el-pagination
          v-if="unionList && unionList.length > 0"
          @size-change="handleSizeChangeunionList"
          @current-change="handleCurrentChangeunionList"
          :current-page="unioncurrentPage"
          :page-size="unionpageNum"
          background
          layout="total, sizes, prev, pager, next, jumper"
          :total="uniontotalCount">
        </el-pagination>
      </div>
    </div>
  </div>
</template>

<script>
// import {dateFormat} from '@/filters/index'
export default {
  name: 'partyBuild',
  data () {
    return {
      newList: [],
      unionList: [],
      currentPage: 0,
      totalCount: 0,
      keyword: '',
      pageNum: 10,
      unioncurrentPage: 0,
      uniontotalCount: 0,
      unionkeyword: '',
      unionpageNum: 10
    }
  },
  watch: {
    keyword () {
      this.getList()
    },
    unionkeyword () {
      this.getUnionList()
    }
  },
  methods: {
    handleSizeChange (val) {
      this.pageNum = val
      this.getList()
    },
    handleCurrentChange (val) {
      this.currentPage = val
      this.getList()
    },
    handleSizeChangeunionList (val) {
      this.pageNum = val
      this.getList()
    },
    handleCurrentChangeunionList (val) {
      this.currentPage = val
      this.getList()
    },
    toDetail (item) {
      window.sessionStorage.partDetail = JSON.stringify(item)
      this.$router.push('/partyBuildDetail')
    },
    async getList () {
      this.newList = []
      await this.$store.dispatch('articleGet', {
        pageSize: this.pageNum,
        pageNum: this.currentPage,
        type: 4,
        titleofarticle: this.keyword
      }).then((res) => {
        if (res && res.list && res.list.length > 0) {
          this.newList = res.list
          this.totalCount = res.total
        }
      })
    },
    async getUnionList () {
      this.unionList = []
      await this.$store.dispatch('articleGet', {
        pageSize: this.unionpageNum,
        pageNum: this.unioncurrentPage,
        type: 7,
        titleofarticle: this.unionkeyword
      }).then((res) => {
        if (res && res.list && res.list.length > 0) {
          this.unionList = res.list
          this.uniontotalCount = res.total
        }
      })
    }
  },
  async created () {
    await this.getList()
    await this.getUnionList()
  }
}
</script>

<style lang="scss">
.partyBuild {
  padding: 20px 0;
  .clear-left {
    width: 50%;
    float: left;
    height: 100%;
    border-right: 1px dashed #ccc;
    box-sizing: border-box;
    .top-title {
      text-align: left;
      background: #ee0000;
    }
    .clear-content {
      padding-right: 10px;
    }
  }
  .clear-right {
    width: 50%;
    float: right;
    height: 100%;
    .top-title {
      text-align: left;
      background: #ee0000;
    }
    .clear-content {
      padding-left: 10px;
    }
  }
  .clear-content {
    min-height: calc(100vh - 500px);
  }
}
</style>



